            //background: none;
            text-transform: uppercase;
        }
        // MENU ITEM HOVERS
        li a:not(.button):hover {
          color: $white;
        }
        // MENU ITEM ACTIVE
        li.active a:not(.button) {
            &:hover {
                //background-color: red;
            }
        }
        li {
            a {
            }
        }
    }
    // DROPDOWN MENU
    .top-bar-section ul li:hover:not(.has-form) > a {
        color: #333;
